<template>
  <div id="collect">
    <div>
      <p>总人数：{{info.totalPeople}}</p>
      <p>总分数：{{info.sum}}</p>
      <p>平均分：{{info.average}}</p>
    </div>
    <el-button type="primary" size="medium" @click="search">刷新</el-button>
  </div>
</template>

<script>
import {getCollect} from '@/api/collect'
export default {
  name: 'collect',
  props: ['activeName'],
  data () {
    return {
      info: {
        totalPeople: '',
        sum: '',
        average: ''
      }
    }
  },
  watch: {
    activeName (val) {
      if (val === '2') {
        this.search()
      }
    }
  },
  methods: {
    search () {
      this.getCollectFun()
    },
    sumFun (arr) {
      let sum = 0
      arr.map(item => {
        sum = sum + item.mark
      })
      return sum
    },
    async getCollectFun () {
      let {data} = await getCollect()
      if (data.code === 1 && data.results) {
        let results = data.results
        this.info.totalPeople = results.length
        this.info.sum = this.sumFun(results)
        let average = (this.info.sum / this.info.totalPeople).toFixed(2)
        this.info.average = average !== 'NaN' ? average : 0
      }
    }
  }
}
</script>

<style scoped lang="scss">
  #collect {
    padding: .2rem;
  }
</style>



